import { Pipe, PipeTransform } from '@angular/core';
import {DomSanitizer, SafeHtml, SafeUrl} from '@angular/platform-browser';

@Pipe({
  name: 'safe'
})
export class HTMLPipePipe implements PipeTransform {

  constructor(private domSanitizer: DomSanitizer) {
  }

  /**
   * use DomSanitizer allow insert outside HTML
   * ä½¿ç¨DomSanitizeråè®¸æå¥å¤é¨çHTML
   * @param value - html content htmlåå®¹
   * @param args = ç¬¬ä¸ä¸ªåæ°æ¯åå®¹ç±»å`html/url` é»è®¤ä¸º `html`
   * @return - transformed html content htmlåæ¢åçåå®¹
   */
  transform(value: any, ...args: any[]): SafeHtml | SafeUrl {
    const type = args[0];
    switch (type) {
      case 'html':
        return this.domSanitizer.bypassSecurityTrustHtml(value);
      case 'url':
        return this.domSanitizer.bypassSecurityTrustUrl(value);
      default:
        return this.domSanitizer.bypassSecurityTrustHtml(value);
    }
  }

}
